import { prev } from '../../Helpers/Value';
import template from '../../Helpers/Template';
import ListItem from '../../Components/ListItem';
import { createElement, appendChildren } from '../../Helpers/Template';

export default function(instance) {
    const el = createElement('div', {
        class: 'flip-clock-list'
    });

    return appendChildren(el, [
        instance.createListItem(prev(instance.value)).render(),
        instance.createListItem(instance.value).render()
    ]);
}
