import Component from './Component';
import digitize from '../Helpers/Digitize';
import { next, prev } from '../Helpers/Value';
import { isObject, isNumber } from '../Helpers/Functions';

export default class FaceValue extends Component {

    constructor(value, attributes) {
        super(Object.assign({
            prependLeadingZero: isNumber(value),
            minimumDigits: 0,
            value: value
        }, attributes));
    }

    get digits() {
        return this.$digits;
    }

    get value() {
        return this.$value;
    }

    set value(value) {
        this.$digits = digitize(this.$value = value, {
            minimumDigits: this.minimumDigits,
            prependLeadingZero: this.prependLeadingZero
        });
    }

    next(attributes) {
        const defaults = this.getAttributes();

        delete defaults.$value;
        delete defaults.$digits;

        return new this.constructor(
            next(this.value), Object.assign(defaults, attributes)
        );
    }

    prev(attributes) {
        const defaults = this.getAttributes();

        delete defaults.$value;
        delete defaults.$digits;

        return new this.constructor(
            prev(this.value), Object.assign(defaults, attributes)
        );
    }

}
