import { createElement, appendChildren } from '../../Helpers/Template';

export default function(instance) {
    const children = [];

    const el = createElement('div', {
        class: 'flip-clock'
    });

    instance.value.digits.forEach((grouping, i) => {
        if(i > 0) {
            children.push(instance.createDivider().render());
        }

        grouping.forEach(value => {
            children.push(instance.createList(value).render());
        });
    });

    appendChildren(el, children);

    return el;
}
