import Component from './Component';
import validate from '../Helpers/Validate';
import translate from '../Helpers/Translate';
import { error } from '../Helpers/Functions';
import { kebabCase } from '../Helpers/Functions';
import { createElement } from '../Helpers/Template';
import ConsoleMessages from '../Config/ConsoleMessages';

export default class DomComponent extends Component {

    constructor(attributes) {
        super(Object.assign({
            el: null
        }, attributes));

        if(!this.theme) {
            error(`${this.constructor.name} does not have a theme defined.`);
        }

        if(!this.language) {
            error(`${this.constructor.name} does not have a language defined.`);
        }
    }

    get el() {
        return this.$el;
    }

    set el(value) {
        if(!validate(value, null, HTMLElement)) {
            error(ConsoleMessages.element);
        }

        this.$el = value;
    }

    get theme() {
        return this.$theme;
    }

    set theme(value) {
        if(!validate(value, 'object')) {
            error(ConsoleMessages.value);
        }

        this.$theme = value;
    }

    get language() {
        return this.$language;
    }

    set language(value) {
        if(!validate(value, 'object')) {
            error(ConsoleMessages.language);
        }

        this.$language = value;
    }

    translate(key) {
        return translate(key, this.language);
    }

    t(key) {
        return this.translate(key);
    }

	render() {
		if(this.theme[this.constructor.name]) {
            return this.el = this.theme[this.constructor.name](this);
        }

        throw new Error(
            'This component cannot be rendered because it has no template.'
        );
	}

    mount(parent) {
        if(!this.el) {
            this.render();
        }

        parent.appendChild(this.el);
    }

}
